import React from "react";
import '../App.css';
import { HashRouter as Router , Route, Switch, withRouter } from "react-router-dom";
import Homepage from "../Homepage";
import DataLabeling from "../components/DataLabeling";
import DamageAnalysis from "../components/DamageAnalysis";
import AiConsulting from "../components/AiConsulting";
import Demo from "./Demo";
import ImageVisualizer from "./ImageVisualizer";
import ReactGA from 'react-ga';

const trackPage = (page) => {
    ReactGA.set({
      page
    });
    ReactGA.pageview(page);
  };

class Main extends React.Component {
constructor(props){
		super(props);
		this.state = {};
	}
  render() {
    return (
      <Router>
      <Switch>
        <Route
        path="/data-labeling"
         render={
          props=>{
          const page = this.props.location.pathname;
          trackPage(page);
          return (
            <DataLabeling browserLang={this.props.browserLang} {...this.props}></DataLabeling>
            );
          }
         }
         >
         </Route>
          <Route
        path="/pavements-analysis"
         render={
          props=>{
            const page = this.props.location.pathname;
            trackPage(page);
            return (
            <DamageAnalysis browserLang={this.props.browserLang} {...this.props}></DamageAnalysis>
          );}
         }
         >
         </Route>
         <Route
        path="/ai-consulting"
         render={
          props=>{
            const page = this.props.location.pathname;
            trackPage(page);
            return (
            <AiConsulting browserLang={this.props.browserLang} {...this.props}></AiConsulting>
          )}
         }
         >
         </Route>
         <Route
        path="/demo"
         render={
          props=>{
            const page = this.props.location.pathname;
            trackPage(page);
            return(
            <Demo browserLang = {this.props.browserLang} {...this.props}></Demo> 
          )}
         }
         >
         </Route>
         <Route
        path="/visualize"
         render={
          props=>{
            const page = this.props.location.pathname;
            trackPage(page);
            return (
            <ImageVisualizer browserLang = {this.props.browserLang} {...this.props}></ImageVisualizer> 
          )}
         }
         >
         </Route>
        <Route
        exact path="/"
         render={
          props=>{
            const page = this.props.location.pathname;
            trackPage(page);
            return(
            <Homepage browserLang={this.props.browserLang} {...this.props}></Homepage>
          )}
         }
         >
          
        </Route>
      </Switch>
      
      </Router>
      
      );
  }
}

export default withRouter(Main);